import React from "react"
import i18n from "../lib/i18n"
import usFlag from "../images/flags/us.svg"
import esFlag from "../images/flags/es.svg"

export default function LanguageSwitcher() {
  const changeLanguage = lng => {
    i18n.changeLanguage(lng)
  }

  return (
    <div className="flex justify-end mr-4 mt-4 lg:mr-4 lg:mt-4">
      <button className="spanish-text mr-1" onClick={() => changeLanguage("en")}>EN&nbsp;<span className="flag-icon" style={{ backgroundImage: `url(${usFlag})` }} /></button> |{" "}
      <button className="english-text ml-1" onClick={() => changeLanguage("es")}>ES&nbsp;<span className="flag-icon" style={{ backgroundImage: `url(${esFlag})` }} /></button>
    </div>
  )
}
